<template>
  <div>
    <div class="flex flex-col md:flex-row mb-6">
      <div class="flex-grow md:mr-3 md:mb-0 mb-3">
        <Input v-model="data.search" placeholder="Search Everything" class="w-full" icon>
          <template #icon>
            <span class="material-icons">search</span>
          </template>
        </Input>
      </div>
      <div class="flew-grow-0">
        <GroupContainer>
          <Button color="white" view="outlined" class="flex-auto" @click="filterDateFocus" @blur="filterDateFocus">
            <DatePicker v-model="data.date" v-bind="filterDateOptions" type="daterange" ref="filterDateRef" class="hide-picker" /> Select Date
          </Button>
          <Dropdown trigger="click" size="medium" :hide-on-click="false">
            <Button color="white" view="outlined" class="flex-auto dropdown dropdown-arrow">Status</Button>
            <template #dropdown>
              <DropdownMenu>
                <DropdownItem v-for="(status, statusIndex) in invoiceStatuses" :key="statusIndex">
                  <Checkbox color="teal" v-model="data.status_id" :value="status.id">
                    {{ status.name }}
                  </Checkbox>
                </DropdownItem>
              </DropdownMenu>
            </template>
          </Dropdown>
          <Dropdown trigger="click" size="medium" :hide-on-click="false" :disabled="loadingSalesPerson">
            <Button color="white" view="outlined" class="flex-auto dropdown dropdown-arrow" :loading="loadingSalesPerson">Sales Person</Button>
            <template #dropdown>
              <DropdownMenu>
                <DropdownItem v-for="(stateSales, index) in stateSalesPerson" :key="index">
                  <Checkbox color="teal" v-model="data.sales_person" :value="stateSales">
                    {{ stateSales.sales_person }}
                  </Checkbox>
                </DropdownItem>
              </DropdownMenu>
            </template>
          </Dropdown>
        </GroupContainer>
      </div>
    </div>
    <div class="flex items-center flex-row mb-6">
      <div class="inline-block" v-for="(filter, filterKey) of filterList" :key="filterKey">
        <Button v-if="filter.value" color="grey" view="smooth" dense class="mx-1">
          <b>{{filter.name}}:</b> {{ filter.value }} <i class="material-icons text-sm ml-2" @click="clearFilter(filter)">close</i>
        </Button>
      </div>
      <div class="inline-block ml-3">
        <Button v-if="isFiltered" @click="clearAllFilter" color="teal" view="smooth" dense>
          <i class="material-icons text-sm mr-1">close</i>Clear all
        </Button>
      </div>
    </div>
    <Table class="mb-6" :data="table.data" v-loading="loading" highlight-current-row>
      <TableColumn prop="box_id" label="Invoice#">
        <template #default="scope">
          <router-link tag="a" class="font-bold" :to="{ name: 'invoiceDetail', params: { buybackId: scope.row.box_id } }">{{ scope.row.box_id }}</router-link>
        </template>
      </TableColumn>
      <TableColumn v-if="data.status_id == 'all'" prop="status_invoice" label="Status" width="100">
        <template #default="scope">
          <Badge class="whitespace-nowrap" :color="scope.row.status_invoice == 'Paid' ? 'teal' : 'red'">{{ scope.row.status_invoice }}</Badge>
        </template>
      </TableColumn>
      <TableColumn label="Bill To" width="150">
        <template #default="scope">
          <TextLimit :text="scope.row.customer_fullname || ''" />
        </template>
      </TableColumn>
      <TableColumn prop="sales_person" label="Sales Person" width="150">
        <template #default="scope">
          <TextLimit :text="scope.row.sales_person || ''" />
        </template>
      </TableColumn>
      <TableColumn prop="invoice_date" label="Invoice Date" width="100" />
      <TableColumn width="100" :label="data.status_id == '3' && 'Date Paid' || 'Due Date'">
        <template #default="scope">
          {{ data.status_id == 3 ? scope.row.closed_date : scope.row.due_date }}
        </template>
      </TableColumn>
      <TableColumn prop="sub_total" label="Total" width="200" align="right">
        <template #default="scope">
          <Currency :value="scope.row.sub_total" />
        </template>
      </TableColumn>
      <TableColumn v-if="data.status_id == '1'" prop="balance_due" label="Balance Due" width="200" align="right">
        <template #default="scope">
          <Currency :value="scope.row.balance_due" />
        </template>
      </TableColumn>
      <TableColumn prop="profit" v-if="data.status_id == '3' || data.status_id == '1'" label="Profit" width="150" align="right">
        <template #default="scope">
          <Currency :value="scope.row.profit" />
        </template>
      </TableColumn>
      <TableColumn prop="profit_percentage" v-if="data.status_id == '3'" label="Profit Margin" width="100" align="right">
        <template #default="scope">
          {{scope.row.profit_percentage}}%
        </template>
      </TableColumn>
      <TableColumn v-if="data.status_id != '3'" prop="shipping_tracking" label="Tracking Number" width="300">
        <template #default="scope">
          {{ scope.row.shipping_tracking }}
        </template>
      </TableColumn>
    </Table>
    <Pagination v-model:page="data.page" v-model:limit="data.limit" :data="table" />
  </div>
</template>

<script>
import { defineComponent, watch } from 'vue'
import { useRoute } from 'vue-router'
import { useInvoice } from 'composable/customer'
import debounce from 'utils/debounce'
import Badge from 'bbUi/components/badge/badge.vue'
import GroupContainer from 'bbUi/components/group-container/group-container.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Tab from 'bbUi/components/tab/tab.vue'
import Pagination from 'components/Pagination.vue'
import Currency from 'components/Currency.vue'
import TextLimit from 'components/TextLimit.vue'

export default defineComponent({
  components: {
    Badge,
    GroupContainer,
    Button,
    Input,
    Checkbox,
    Select,
    Pagination,
    Tab,
    Currency,
    TextLimit
  },
  async setup() {
    const { data, loading, loadingSalesPerson, state: table, stateSalesPerson, invoiceStatuses, action, actionSalesPerson, filterDateRef, filterDateOptions, filterDateFocus, filterList, isFiltered, clearFilter, clearAllFilter } = useInvoice()
    const route = useRoute()

    watch(() => ({
      limit: data.limit,
      search: data.search,
      sales_person: data.sales_person,
      date: data.date,
      status_id: data.status_id,
    }), () => {
      data.page = 1
    }, {
      deep: true
    })
    watch(() => ({
      data, 
      category: route.params.category
    }), debounce(async () => {
      if(route.params.category == 'invoice'){
        await Promise.all([
          action(route.params.customerId)
        ])

  
      }
    }, 500), {
      immediate: true,
      deep: true
    })

    const tableSummary = ({columns}) => {
      return columns.map(column => column.property === 'sub_total' ?  `${currency(me.value, table.value.total_displayed)}` : '' || column.property === 'qty' ? 'Total' : '')
    }

    actionSalesPerson()

    return {
      data,
      table,
      loading,
      loadingSalesPerson,
      route,
      tableSummary,
      filterDateRef,
      filterDateOptions,
      filterDateFocus,
      invoiceStatuses,
      stateSalesPerson,
      filterList,
      isFiltered,
      clearFilter,
      clearAllFilter
    }
  }
})
</script>