<template>
  <div>
    <div>
      <Tab view="smooth" :items="tab" :validate="validateMenus" />
    </div>
    <div v-if="['in-store', 'mail-in'].includes(route.params.category)">
      <div class="flex flex-col md:flex-row mb-6">
        <div class="flex-grow md:mr-3 md:mb-0 mb-3">
          <Input v-model="data.search" placeholder="Search Everything" class="w-full" icon>
            <template #icon>
              <span class="material-icons">search</span>
            </template>
          </Input>
        </div>
        <div class="flew-grow-0">
          <GroupContainer>
            <Button color="white" view="outlined" class="flex-auto" @click="filterDateFocus" @blur="filterDateFocus">
              <DatePicker v-model="data.date" v-bind="filterDateOptions" type="daterange" ref="filterDateRef" class="hide-picker" /> Select Date
            </Button>
          <Dropdown trigger="click" size="medium" :hide-on-click="false">
              <Button color="white" view="outlined" class="flex-auto dropdown dropdown-arrow">Status</Button>
              <template #dropdown>
                <DropdownMenu>
                  <DropdownItem v-for="(status, statusIndex) in statuses" :key="statusIndex">
                    <Checkbox color="teal" v-model="data.statuses" :value="status">
                      {{ status.status_name }}
                    </Checkbox>
                  </DropdownItem>
                </DropdownMenu>
              </template>
            </Dropdown>
            <Dropdown trigger="click" size="medium" :hide-on-click="false" :disabled="loadingPaymentMethods">
              <Button color="white" view="outlined" class="flex-auto dropdown dropdown-arrow" :loading="loadingPaymentMethods">Payout Method</Button>
              <template #dropdown>
                <DropdownMenu>
                  <DropdownItem v-for="(paymentMethod, index) in statePaymentMethods" :key="index">
                    <Checkbox color="teal" v-model="data.payment_method" :value="paymentMethod.name">
                      {{ paymentMethod.name }}
                    </Checkbox>
                  </DropdownItem>
                </DropdownMenu>
              </template>
            </Dropdown>
            <Dropdown trigger="click" size="medium" :hide-on-click="false" v-if="route.params.category === 'mail-in'">
                <Button color="white" view="outlined" class="flex-auto dropdown dropdown-arrow">Courier</Button>
                <template #dropdown>
                  <DropdownMenu>
                    <DropdownItem v-for="(courier, courierIndex) in couriers" :key="courierIndex">
                      <Checkbox color="teal" v-model="data.shipping_courier" :value="courier.name">
                        {{ courier.name }}
                      </Checkbox>
                    </DropdownItem>
                  </DropdownMenu>
                </template>
              </Dropdown>
          </GroupContainer>
        </div>
        <!-- <div v-if="me.package_id > 2" class="flex-grow-0 self-center ml-4">
          <Checkbox v-model="data.is_in_store" type="toggle" color="teal" value="1" value-off="0">Mail-in</Checkbox>
        </div> -->
      </div>
      <div class="flex items-center flex-row mb-6">
        <div class="inline-block" v-for="(filter, filterKey) of filterList" :key="filterKey">
          <Button v-if="filter.value" color="grey" view="smooth" dense class="mx-1">
            <b>{{filter.name}}:</b> {{ filter.value }}<i class="material-icons text-sm ml-2" @click="clearFilter(filter)">close</i>
          </Button>
        </div>
        <div class="inline-block ml-3">
          <Button v-if="isFiltered" @click="clearAllFilter" color="teal" view="smooth" dense>
            <i class="material-icons text-sm mr-1">close</i>Clear all
          </Button>
        </div>
      </div>
      <Table class="mb-6" :data="table.data" v-loading="loading" highlight-current-row @current-change="redirectToOrder" show-summary :summary-method="tableSummary">
        <TableColumn prop="box_id" label="Buyback ID#" width="150">
          <template #default="scope">
            <router-link tag="a" class="font-bold" :to="{ name: scope.is_local === 1 ? 'buybackLocalDetail' : 'buybackOnlineDetail', params: { buybackId: scope.row.box_id } }">{{ scope.row.box_id }}</router-link>
          </template>
        </TableColumn>
        <TableColumn v-if="route.name !== 'customerDetail'" prop="name" label="Name" width="300">
          <template #default="scope">
            {{ scope.row.name || scope.row?.customer?.email || '' }}
          </template>
        </TableColumn>
        <TableColumn prop="order_status.short_name" label="Status">
          <template #default="scope">
            <Badge class="whitespace-nowrap" :color="color(scope.row.order_status.short_name)">
              {{ scope.row.order_status.short_name }}
            </Badge>
          </template>
        </TableColumn>
        <TableColumn prop="order_date" label="Status Date">
          <template #default="scope">
            {{ scope.row.order_date }}
          </template>
        </TableColumn>
        <TableColumn prop="payment_method" label="Payout Method" width="150" />
        <TableColumn prop="shipping_courier" label="Courier" v-if="route.params.category === 'mail-in'" />
        <TableColumn prop="qty" label="Qty" align="right" />
        <TableColumn prop="sub_total" label="Total" align="right">
          <template #default="scope">
            <Currency :value="scope.row.sub_total" />
          </template>
        </TableColumn>
      </Table>
      <Pagination v-model:page="data.page" v-model:limit="data.limit" :data="table" />
    </div>
    <Invoice v-if="route.params.category == 'invoice'"/>
  </div>
</template>

<script>
import { defineComponent, watch, computed } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { usePaymentMethods } from 'composable/setting'
import { useBuyback, useInvoice } from 'composable/customer'
import { useStatuses } from 'composable/buyback'
import { useUser } from 'composable/user'
import debounce from 'utils/debounce'
import { color } from 'utils/status'
import { currency } from 'utils/number'
import Badge from 'bbUi/components/badge/badge.vue'
import GroupContainer from 'bbUi/components/group-container/group-container.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Tab from 'bbUi/components/tab/tab.vue'
import Pagination from 'components/Pagination.vue'
import Currency from 'components/Currency.vue'
import Invoice from 'components/Customer/Detail/Invoice.vue'

export default defineComponent({
  components: {
    Badge,
    GroupContainer,
    Button,
    Input,
    Checkbox,
    Select,
    Pagination,
    Tab,
    Currency,
    Invoice
  },
  props: {
    customer: {
      type: Boolean,
      default: false
    }
  },
  async setup(props) {
    const { state: statePaymentMethods, loading: loadingPaymentMethods, action: actionPaymentMethods } = usePaymentMethods()
    const { tab, data, state: table, action, filterList, clearFilter, clearAllFilter, isFiltered, filterDateRef, filterDateOptions, filterDateFocus, loading  } = useBuyback()
    const { data: dataInvoice, loading: loadingInvoice, state: stateInvoice, stateSalesPerson, action: actionInvoice, actionSalesPerson  } = useInvoice()
    const { state: stateOnlineStatuses, action: actionOnlineStatuses } = useStatuses('online')
    const { state: stateLocalStatuses, action: actionLocalStatuses } = useStatuses('local')
    const { me } = useUser()
    const route = useRoute()
    const router = useRouter()
    const couriers = [{
      id: 'USPS',
      name: 'USPS'
    },{
      id: 'FedEx',
      name: 'FedEx'
    },{
      id: 'UPS',
      name: 'UPS'
    }]

    watch(() => ({
      limit: data.limit,
      search: data.search,
      payment_method: data.payment_method,
      date: data.date,
      courier: data.shipping_courier,
      status: data.statuses,
      category: route.params.category
    }), () => {
      data.page = 1
    }, {
      deep: true
    })
    watch(() => ({
      data, 
      category: route.params.category
    }), debounce(async () => {
      const isInStore = route.params.category === 'in-store'
      if( !isInStore && me.value.package_id < 3 ) router.push({
        name: route.name,
        params: {
          ...route.params,
          category: 'in-store'
        }
      })

      if(['in-store', 'mail-in'].includes(route.params.category)){
        await Promise.all([
          await action(route.params.customerId, isInStore)
        ])
      }
    }, 500), {
      immediate: true,
      deep: true
    })

    actionPaymentMethods()

    const tableSummary = ({columns}) => {
      return columns.map(column => column.property === 'sub_total' ?  `${currency(me.value, table.value.total_displayed)}` : '' || column.property === 'qty' ? 'Total' : '')
    }
    const redirectToOrder = (data) => {
      router.push({
        name: data.is_local === 1 ? 'buybackLocalDetail' : 'buybackOnlineDetail',
        params: {
          buybackId: data.box_id
        }
      })
    }
    const validateMenus = (menu) => {
      return menu.id === 'mail-in' ? me.value.package_id > 2 : true // show mail-in only when package_id is greater than 1
    }
    const statuses = computed(() => route.params.category === 'in-store' ? stateLocalStatuses.value : stateOnlineStatuses.value)
    await Promise.all([
      await actionOnlineStatuses(),
      await actionLocalStatuses()
    ])
    return {
      tab,
      data,
      couriers,
      loading,
      table,
      statuses,
      stateOnlineStatuses,
      stateLocalStatuses,
      filterList,
      isFiltered,
      statePaymentMethods,
      loadingPaymentMethods,
      actionPaymentMethods,
      filterDateRef,
      filterDateOptions,
      filterDateFocus,
      color,
      me,
      clearFilter,
      clearAllFilter,
      tableSummary,
      redirectToOrder,
      validateMenus,
      route
    }
  }
})
</script>